.mask {
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  z-index: 100;
  background-color: #000;
  justify-content: center;
  align-items: center;
  transition: all 1s;
  display: flex;
  position: fixed;
  top: 0;
}

.mask .loader {
  width: 75px;
  height: 75px;
  border: 10px solid #dc56c600;
  border-left-color: #dc56c5;
  border-radius: 50%;
  margin: auto;
  animation: 1s linear infinite loader;
}

@keyframes loader {
  100% {
    transform: rotate(360deg);
  }
}

.hide {
  opacity: 0;
}

