@media screen and (min-width: 320px){
    // ==================================================== modals ===================================================================
    .modal {
        display: none;
        position: fixed;
        overflow: auto;
        z-index: 1;
        top: 0;
        background-color: rgba(0, 0, 0, 0.8);
        width: 100%;
        height: 100%;
        margin: 0 auto;
        justify-content: center;       
        z-index: 3;
        &-window {
            position: relative;
            top: 2%;
            background-color: #FFF2FD;
            width: 272px;
            height: 880px;
            border-top-left-radius: 150px;
            border-bottom-right-radius: 150px;
        }
    }

    .svg-close {
        color: #4C00FE;
        font-size: 28px;
        font-weight: bold;
        margin-left: 230px;
        margin-top: 25px;

        &:hover,
        &:focus {
            color: black;
            text-decoration: none;
            cursor: pointer;
        }
    }

    .head-image{
        height: 120px;
        display: flex;
        justify-content: center;
        align-items: center;
        object-fit: initial;
        
        & img {
            object-fit: initial;
            height: 90px;
            width: 90px;
            border-radius: 100%;
        }
    }

    .main-image {
        display: none;
    }

    .info {
        display: flex;
        flex-direction: column;
        align-items: center;
        
        & h2 {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 24px;
            color: #DC56C5;
        }

        & p {
            overflow-y: scroll;
            height: 60px;
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            color: #0E0E0E;
            margin-top: 5px;
            width: 243px;
            -ms-overflow-style: none;  /* IE and Edge */
            scrollbar-width: none;  /* Firefox */
            &::-webkit-scrollbar{
                display: none;
            }
        }
    }

    .when {
        display: flex;
        flex-direction: column;
        align-items: center;

        & h2 {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            color: #DC56C5;
            margin-top: 10px;
        }

        & p {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            color: #0E0E0E;
            margin-top: 4px;
        }
    }

    .where {
        display: flex;
        flex-direction: column;
        align-items: center;

        & h2 {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            color: #DC56C5;
            margin-top: 10px;
        }

        & p {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            color: #0E0E0E;
            margin-top: 4px;
            max-height: 40px;
            overflow-y: scroll;
        }
    }

    .who {
        display: flex;
        flex-direction: column;
        align-items: center;

        & h2 {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            color: #DC56C5;
            margin-top: 10px;
        }

        & p {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            color: #0E0E0E;
            margin-top: 4px;
            max-height: 40px;
            overflow-y: scroll;
        }
    }

    .prices {
        display: flex;
        flex-direction: column;
        align-items: center;

        & h2 {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            color: #DC56C5;
            margin-top: 10px;
        }
    }

    .standart-ticket {
        display: flex;
        flex-direction: row;
        margin-top: 5px;

        & svg {
            height: 16px;
            margin-right: 13px;
        }

        & p {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            color: #0E0E0E;
        }
    }

    .vip-ticket {
        display: flex;
        flex-direction: row;
        margin-top: 20px;

        & svg {
            height: 16px;
            margin-right: 13px;
        }

        & p {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            color: #0E0E0E;
        } 
    }

    .buy-tickets {
        background: #4C00FE;
        border-radius: 5px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        color: #FFFFFF;
        padding: 10px 19px;
        border: solid 1px #4C00FE;
        margin-top: 10px;
        transition: color .3s ease-in-out, background-color .3s ease-in-out, border-color .3s ease-in-out;

        &:active, &:hover {
            color: #4C00FE;
            background-color: #FFFFFF;
            border-color: #4C00FE;
        }
    }

    .more-information {
        & button {
            border: 1px solid #4C00FE;
            border-radius: 5px;
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            color: #4C00FE;
            background-color: white;
            padding: 3px 10px;
            height: 40px;
            margin-top: 25px;
            margin-left: 13px;
            transition: border .3s ease-in-out;

            &:active, &:hover {
                border: 1px solid #DC56C5;
            }
        }
    }
// =================================================================================================================

    .main-section1{
        display: flex;
        justify-content: center;
    }
    .list-cards{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 320px;
    }
    .event{
        width: 138px;
        height: 242px;
        list-style: none;
        padding: 10px 9px 0 0;
        margin-bottom: 40px;
        // overflow: scroll;
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
        // transition: trasnslateY() 5s ease-out;
        transition-property: transform;
        transition-duration: 300ms;
        transition-timing-function: ease-out;
    
        &::-webkit-scrollbar {
            display: none;
        }
        &:hover{
            transform: translateY(-8);
        }
        &:nth-child(odd){
            margin-left: 10px;
        }
    }

    .ramka-event{
        pointer-events: none;
        width: 108px;
        height: 101px;
        border: 1px solid rgba(220, 86, 197, 0.3);
        border-radius: 50px 0px;
        margin-bottom: -90px;
        margin-left: 30px;
        box-sizing: border-box;
        position: relative;
        z-index: 0;
    }
    .event img{
        object-fit: cover;
        width: 128px;
        height: 161px;
        border-radius: 50px 0px;
        object-fit: cover;
        position: relative;
        z-index: 1;
    }
    .event-name{
        min-height: 20px;
        max-height: 45px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #DC56C5;
        margin-top: 5px;
        overflow-y: scroll;
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
        &::-webkit-scrollbar {
            display: none;
        }
    }
    .event-h4{
        // height: 14px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #FFFFFF;
        margin: 5px 0;
    }
    .event-location{
        // height: 12px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 15px;
        color: #FFFFFF;
        text-align: center;
    }

    



@media screen and (min-width: 768px){
// ==================================================== modals ===================================================================
    .modal-window {
        width: 595px;
        height: 831px;
        
    }

    .svg-close {
        margin-top: 20px;
        margin-left: 543px;
    }

    .center {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        margin-top: 36px;
    }

    .head-image {
        height: 103px;
    }

    .main-image {
        display: block;

        & img {
            height: 416px;
            width: 233px;
        }
    }

    .info {
        display: flex;
        align-items: flex-start;

        & p {
            width: 265px;
            height: 100px;
            text-align: start;
        }
    }

    .when {
        align-items: flex-start;
    }

    .where {
        align-items: flex-start;
    }

    .who {
        align-items: flex-start;
    }

    .prices {
        align-items: flex-start;
    }

    .more-information {
        margin-top: 13px;
        margin-left: 165px;
    }
// =======================================================================================================================

    .list-cards{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 650px;
    }
    .event{
        width: 196px;
        height: 312px;
        list-style: none;
        padding: 13px 14px 0 0;
        margin-bottom: 60px;
    }
    .event:nth-child(odd){
        margin-left: 0;
    }
    .ramka-event{
        width: 153px;
        height: 143px;
        margin-bottom: -130px;
        margin-left: 40px;
    }
    .event img{
        width: 180px;
        height: 227px;
    }
    .event-name{
        // height: 20px;
        font-size: 16px;
        line-height: 20px;
        max-height: 55px;
    }
    .event-h4{
        // height: 20px;
        font-size: 16px;
        line-height: 20px;
    }
    .event-location {
        // height: 17px;
        font-size: 14px;
        line-height: 17px;
    }
}


