//! ==================== pagination ====================
.pagination{
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 5px;
}

.pag-but{
    border: 1px solid transparent;
    background-color: transparent;
    border-radius: 10px 0;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #FFFFFF; 
    transition: .3s border ease-in-out, .3s color ease-in-out;
    

    &:hover{
        border: 1px solid #DC56C5;
        color: #DC56C5;
    }
}

}